  import Step from "../../../base/step/step.vue"
  import Scroll from '../../../base/scroll/scroll.vue'
  import Upload from '../../../base/upload/upload.vue'
  import { formateDate } from '../../../assets/js/utils'

  export default {
    name: "taskOne_step1",
    components: {
      Step,
      Scroll,
      Upload
    },
    data() {
      return {
        isOk: true, //
        openIt: false,
        favImg: [], //
        focusImg: [], //
        type: 1, //
        stepIndex: 0, //
        msg: "1.\r2.\r3.", //placeholder,
        shopName: "",
        taoKey: "111[][]"
      }
    },
    methods:{
      shopCar(){
        if(this.favImg.length === 0 || this.focusImg.length === 0) {
          this.$vux.alert.show({
            title:'',
            content: '',
          });
          return false
        }
        let that = this;
        this.$axios.post('/api/orderOperate/frontOrderSubmit', {
          buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
          favImg: this.favImg,
          focusImg: this.focusImg
        }).then((res) => {
          console.log(res)
          if (res.data.code=== "200") {
//            this.$router.push({name:'details',params:{id:100}})
            let time = formateDate('yyyy-MM-dd hh:mm:ss')
            this.$router.push({ name:'submitSuccess', query: { openTime: time,type: 'task' } })
          }
        }).catch((error) => {
          this.$vux.alert.show({
            title:'',
            content: '',
          })
        })
      }
    }
  }



